<template>
	<div id="chineseStudy">
		<a-row style="margin: 20px">
			<a-space direction="horizontal">
				<a-input style="width: 120px; text-align: center" addon-before="Scaler"
				         v-model="scaleCoefCache"></a-input>
				<a-input style="width: 120px; text-align: center" addon-before="Duplicate"
				         v-model="duplicateCache"></a-input>
				<span>
					<input style="width: 180px" id="file" type="file"/>
					<a-button @click="refreshStoreBySelectedFile">上传配置</a-button>
				</span>
			</a-space>
		</a-row>

		<a-tabs style="margin-left: 20px">
			<a-tab-pane  v-for="i in tabQuant" :key="i" :tab="'Tab ' + i">
				<homework-content :tab-index="i"></homework-content>
			</a-tab-pane>
		</a-tabs>

	</div>
</template>

<script>
import homeworkContent from './homeworkContent.vue'

/* eslint no-mixed-spaces-and-tabs: ["error", "smart-tabs"] */
/* eslint no-unused-vars: [0] */
export default {
    name: 'chineseStudy',
    components: {
        homeworkContent
    },
    data() {
        return {
            scaleCoefCache: null,
            duplicateCache: null,
            tabQuant: 1
        }
    },
    mounted() {
        this.scaleCoefCache = this.$store.getters.getScaleCoef;  // 获取store中默认的Scale值
        this.duplicateCache = this.$store.getters.getDuplicate;  // 获取store中默认的副本数
        this.tabQuant = this.$store.getters.getRecordsQuant;  // 获取store中records数量
    },
    watch: {
        scaleCoefCache(newValue, oldValue){
            let _newValue = parseFloat(newValue)
	        this.$store.commit("updateScaleCoef", {"value": _newValue})
        },
        duplicateCache(newValue, oldValue){
            let _newValue = parseInt(newValue)
            this.$store.commit("updateDuplicate", {"value": _newValue})
        },
    },
    methods: {
        refreshStoreBySelectedFile: function () {
            let that = this;
            let myFile = document.querySelector("#file")
            let reader = new FileReader();
            reader.readAsText(myFile.files[0])
            reader.onload = function () {
                let cache = JSON.parse(reader.result);
                let keys = Object.keys(cache);
                that.tabQuant = keys.length;
                // 更新state
	            let spells_allRecords = {};
	            let words_allRecords = {};
	            let selectAllWordsSwitchStatus_allRecords = {};
	            let selectedWords_allRecords = {};
	            for (let i=1; i<=that.tabQuant; i++){
                    spells_allRecords["record_" + i] = cache["record_" + i]["spells"];
                    words_allRecords["record_" + i] = cache["record_" + i]["words"];
                    selectAllWordsSwitchStatus_allRecords["record_" + i] = cache["record_" + i]["selectAllWords"];
                    selectedWords_allRecords["record_" + i] = cache["record_" + i]["selectedWords"];
	            }
	            that.$store.commit("refreshStateBySelectedFile", {"spells": spells_allRecords, "words": words_allRecords,
		            "selectAllSwitchStatus": selectAllWordsSwitchStatus_allRecords, "selectedWords": selectedWords_allRecords})
            }
        },
    },
    computed: {

    },
    filters: {

    }

}
</script>

